.menu {
    background-image: linear-gradient(to right top, $main-color-light 0%, $main-color-dark 100%);
    padding: 20px;
}

.menu div,
.menu nav ul {
    max-width: $content-width;
    margin: auto;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 75%, rgba(255, 255, 255, 0) 100%);
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.1), inset 0 0 5px rgba(255, 255, 255, 0.6);
    border-radius: 5px;
}

.menu nav ul {
    text-align: right;
}

.menu nav ul li {
    display: inline-block;
    padding: 6px;
}

.menu nav ul li:first-child {
    float: left;
    padding-top: 12px;
}

.menu div,
.menu nav ul li a {
    padding: 12px;
    font-family: sans serif;
    color: white;
}

.menu div {
    text-align: center;
    font-size: 1em;
}

.menu nav ul li a {
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    font-weight: bold;
}

.menu nav ul li:first-child a {
    padding: 5px;
    font-size: 12px;
}

.menu nav ul li a:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), inset 0 0 5px rgba(255, 255, 255, 0.6);
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.2);
    color: white;
}

.menu div a {
    color: white;
}

.menu div a:hover {
    color: $main-color-dark;
}
