@charset "utf-8";

//$base-font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-family: serif;
$base-font-size: 16px;
$spacing-unit:     30px;

$main-color-dark: #031030;
$main-color-light: #2362a1;
//$main-color-dark: #2362a1;
//$main-color-light: #06967c;

// Width of the content area
$content-width:    950px;
$on-palm:          600px;
$on-laptop:        950px;

@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

// Import partials from `sass_dir` (defaults to `_sass`)
@import "site", "about", "menu", "highlight";
