// Main pages
.wrapper {
    max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin: auto;
    //padding-right: $spacing-unit;
    //padding-left: $spacing-unit;
}
.wrapper h1, .wrapper h2, .wrapper h3 { color: $main-color-light; }

// BiBTex plugin
.bibliography a {
    font-size: 1.3em;
}

.bibliography summary {
    font-weight: bold;
    color: $main-color-light;
}

.bibliography summary:hover {
    font-weight: bold;
    overflow: hidden;
    border-radius: 5px;
    background-image: linear-gradient(to right top, $main-color-light 0%, $main-color-dark 100%);
    color: white;
}

.bibliography {
    list-style: none;
    padding-left: 0;
}

.bibliography div {
    padding: 10px;
    background: lightgrey;
    border-radius: 10px;
}

// Images
img {
    max-width: 100%;
    align: middle;
}

// Links
a { color: $main-color-light; }
a:hover { color: $main-color-dark; }

// Blockquote
blockquote {
  border-left: .25em solid darkgrey;
  padding: 0 1em;
}
